.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes visible1 {
  from {
    opacity: 0;
    /* left: 0; */
    transform: translateY(150px) translateX(150px);
  }
  to {
    opacity: 100;
    /* left: 1000; */
    transform: translateY(0px) translateX(0px);
  }
}
@keyframes comingFromRightAn {
  from {
    opacity: 0;
    /* left: 0; */

    transform: translateX(150px);
  }
  to {
    opacity: 100;
    /* left: 1000; */
    transform: translateX(0px);
  }
}
@keyframes comingFromLeftAn {
  from {
    opacity: 0;
    /* left: 0; */
    transform: translateX(-150px);
  }
  to {
    opacity: 100;
    /* left: 1000; */
    transform: translateX(0px);
  }
}

@keyframes comingFromUpAn {
  from {
    opacity: 0;
    /* left: 0; */
    transform: translateY(-150px);
  }
  to {
    opacity: 100;
    /* left: 1000; */
    transform: translateY(0px);
  }
}

@keyframes comingFromDownAn {
  from {
    opacity: 0;
    /* left: 0; */
    transform: translateY(150px);
  }
  to {
    opacity: 100;
    /* left: 1000; */
    transform: translateY(0px);
  }
}

@keyframes growOutAn {
  from {
    opacity: 0;
    width: 0%;
    height: 0%;
  }
  to {
    opacity: 100;
    width: 100%;
    height: 100%;
  }
}

/* .notification {
  animation: visible1 1s;
} */
.growOutP5 {
  animation: growOutAn 0.5s;
}
.growOut {
  animation: growOutAn 1s;
}
.comingFromLeft {
  animation: comingFromLeftAn 1s;
}
.comingFromRight {
  animation: comingFromRightAn 1s;
}
.comingFromDown {
  animation: comingFromDownAn 1s;
}
.comingFromUp {
  animation: comingFromUpAn 1s;
}

.comingFromLeftP5 {
  animation: comingFromLeftAn 0.5s;
}
.comingFromRightP5 {
  animation: comingFromRightAn 0.5s;
}
.comingFromDownP5 {
  animation: comingFromDownAn 0.5s;
}
.comingFromUpP5 {
  animation: comingFromUpAn 0.5s;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.box {
  margin-top: 0;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.spin {
  display: inline-block;
  width: 150px;
  height: 150px;
  border: 10px solid rgba(000, 000, 000, 0.1);
  border-radius: 50%;
  border-top-color: rgba(000, 000, 000, 0.3);
  animation: spin 1s ease-in-out infinite;
  -webkit-animation: spin 1s ease-in-out infinite;
}
@keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
  }
}
@-webkit-keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes arrowRightA {
  0% {
    transform: translateX(0px);
  }
  50% {
    transform: translateX(10px);
  }
  100% {
    transform: translateX(0px);
  }
}

.arrowRight {
  animation: arrowRightA 2s infinite;
}

@keyframes arrowLeftA {
  0% {
    transform: translateX(0px);
  }
  50% {
    transform: translateX(-10px);
  }
  100% {
    transform: translateX(0px);
  }
}

@keyframes arrowLeftA100 {
  0% {
    transform: translateX(0px);
  }
  50% {
    transform: translateX(-100px);
  }
  100% {
    transform: translateX(0px);
  }
}

.arrowLeft {
  animation: arrowLeftA 2s infinite;
}

.arrowLeft100 {
  animation: arrowLeftA100 2s infinite;
}

@keyframes coming {
  from {
    opacity: 0;
    /* left: 0; */
    transform: translateX(-1500px);
  }
  to {
    opacity: 100;
    /* left: 1000; */
    transform: translateX(0px);
  }
}

.notificationSlide {
  animation: coming 1s;
}
